
<template>
  <div class="scrambles-container">
    <h2 v-if="scramble">{{ scramble }}</h2>
    <h2 v-if="!scramble">Generating a fresh scramble...</h2>
    <i
      @click="onSetScramble"
      :class="{ spin: !scramble }"
      class="fa fa-refresh rescramble-btn"
    ></i>
  </div>
</template>

<script>
export default {
  props: {
    scramble: String,
  },
  data() {
    return {};
  },

  mounted() {},

  methods: {
    onSetScramble() {
      this.$emit("setScramble");
    },
  },
};
</script>

