import Vue from 'vue'
import Vuex from 'vuex'
import solveModule from './modules/solve.module.js'
import scrambleModule from './modules/scramble.module.js'
Vue.use(Vuex)

export default new Vuex.Store({
  strict: true,
  state: {
    notification: '',
    timeoutId: null
  },
  getters: {
    notification(state) {
      return state.notification
    }
  },
  mutations: {
    setNotification(state, payload) {
      state.notification = payload.txt;
    }
  },
  actions: {
    showNotification({ commit, dispatch }, { txt }) {
      commit({ type: 'setNotification', txt })
      setTimeout(() => {
        dispatch({ type: 'hideNotification' })
      }, 4000);
    },
    hideNotification({ commit }) {
      commit({ type: 'setNotification', txt: '' })
    },
  },
  modules: {
    solveModule,
    scrambleModule
  }
})
