<template>
  <div v-show="notification" class="notification-container">
    <slot></slot>
    <span @click="close">Close</span>
  </div>
</template>

<script>
export default {
  methods: {
    close() {
      this.$store.dispatch({ type: "hideNotification" });
    },
  },
  computed: {
    notification() {
      return this.$store.getters.notification;
    },
  },
};
</script>

<style lang="scss">
.notification-container {
  z-index: 9999;
  position: fixed;
  left: 20px;
  bottom: 20px;
  background-color: #333333;
  color: #f9f9f9;
  border-radius: 3px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  -webkit-animation: slide-in-bottom 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94)
    both;
  animation: slide-in-bottom 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  & h2 {
    font-weight: 400;
    font-size: 1.2rem;
  }

  & span {
    color: #2d9ffd;
    margin-left: 15px;
    transition: color 0.2s ease;
    cursor: pointer;

    &:hover {
      color: #64b6f8;
    }
  }
}


</style>