<template>
  <div class="solves-table-container">
    <div class="inner-wrapper">
      <div class="solves-table-header">
        <div>
          <span>Solves: {{ solves.length }}</span>
          <span>Best: {{ getFormattedTime(bestSolve) }}</span>
          <span @click="onResetSolves" class="reset-btn">Reset</span>
        </div>
      </div>
      <div v-if="solves.length" class="solves-table-titles">
        <span></span>
        <span>Time</span>
        <span>Ao 5</span>
        <span>Ao 12</span>
      </div>
      <div class="solves-table-results">
        <div v-if="solves.length">
          <solves-table-row
            v-for="(solve, idx) in solves"
            :key="solve._id"
            :solve="solve"
            :idx="idx"
          />
        </div>
        <h2 class="empty-solves-title" v-if="!solves.length">
          Hit the "Space" key and start solving!
        </h2>
      </div>
    </div>
  </div>
</template>

<script>
import { utilService } from "../services/utilService";
import SolvesTableRow from "./SolvesTableRow.vue";
export default {
  props: {
    solves: Array,
    resetSolves: Function,
    bestSolve: Number,
  },
  data() {
    return {};
  },
  methods: {
    onResetSolves() {
      this.$emit("resetSolves");
    },
    getFormattedTime(time) {
      return utilService.getFormattedTime(time);
    },
  },
  computed: {},
  components: {
    SolvesTableRow,
  },
};
</script>

