import { solveService } from '../../services/solveService.js'
export default {
    state: {
        solves: [],
        selectedSolve: null,
    },
    getters: {
        solvesForDisplay(state) {
            return state.solves
        },
        selectedSolve(state) {
            return state.selectedSolve
        }

    },
    mutations: {
        setSolves(state, { solves }) {
            state.solves = solves
        },
        addSolve(state, { newSolve }) {
            state.solves = [...state.solves, newSolve]
        },
        updateSolve(state, { solveToUpdate }) {
            state.solves = state.solves.map(solve => {
                if (solve._id === solveToUpdate._id) {
                    return solveToUpdate
                }
                else return solve
            })
        },
        addComment(state, { commentToSave, solveId }) {
            state.solves = state.solves.map(solve => {
                if (solve._id === solveId) {
                    solve.comments.push(commentToSave)
                    return solve
                }
                else return solve
            })
        },
        removeSolve(state, { solveId }) {
            state.solves = state.solves.filter(solve => solve._id !== solveId)
        },
        resetSolves(state) {
            state.solves = []
        },
        setSelectedSolve(state, { solve }) {
            state.selectedSolve = JSON.parse(JSON.stringify(solve))
        }
    },
    actions: {
        async loadSolves(context) {
            const solves = await solveService.query();
            context.commit({ type: 'setSolves', solves })
        },
        async resetSolves(context) {
            await solveService.resetSolves();
            context.dispatch({
                type: "showNotification",
                txt: "Reset successful",
            });
            context.commit({ type: 'resetSolves' })
        },
        async addSolve(context, { solveResult }) {
            const solve = {
                createdAt: Date.now(),
                penalty: 'good',
                comments: [],
                solveResult,
                scramble: context.getters.scramble,
            };
            const newSolve = await solveService.addSolve(solve);
            context.commit({ type: 'addSolve', newSolve })
        },
        async updateSolve(context, { updatedSolve }) {
            const solveToUpdate = await solveService.updateSolve(updatedSolve);
            context.dispatch({
                type: "showNotification",
                txt: "Solve updated successfully",
            });
            context.commit({ type: 'updateSolve', solveToUpdate })
        },
        async addComment(context, { comment, solveId }) {
            const commentToSave = await solveService.addComment(comment, solveId);
            context.commit({ type: 'addComment', commentToSave, solveId })
        },
        async updatePenalty(context, { penalty }) {
            const solveToUpdate = JSON.parse(JSON.stringify(context.getters.selectedSolve))
            solveToUpdate.penalty = penalty
            await context.dispatch({ type: 'updateSolve', updatedSolve: solveToUpdate })
        },
        async removeSolve(context, { solveId }) {
            await solveService.removeSolve(solveId);
            context.dispatch({
                type: "showNotification",
                txt: "Removed solve successfully",
            });
            context.commit({ type: 'removeSolve', solveId })
        },
        async getSolveById(context, { solveId }) {
            const solve = await solveService.getSolveById(solveId);
            context.commit({ type: 'setSelectedSolve', solve })
        }
    }
}