<template>
  <div class="cube-timer-app-container">
    <solves-table
      @resetSolves="resetSolves"
      :solves="solvesForDisplay"
      :bestSolve="bestSolve"
    />
    <div class="main-timer-screen-container">
      <scramble @setScramble="loadScramble" :scramble="scrambleForDisplay" />
      <timer @onAddSolve="onAddSolve" :bestSolve="bestSolve" />
    </div>
    <notification>
      <h2>{{ notification }}</h2>
    </notification>
    <router-view> </router-view>
  </div>
</template>


<script>
import Scramble from "../components/Scramble.vue";
import Timer from "../components/Timer.vue";
import SolvesTable from "../components/SolvesTable.vue";
import Notification from "../components/Notification.vue";

export default {
  data() {
    return {};
  },

  created() {
    this.loadSolves();
    this.loadScramble();
  },

  methods: {
    async loadSolves() {
      await this.$store.dispatch({ type: "loadSolves" });
    },
    loadScramble() {
      this.$store.commit({ type: "setScramble" });
    },
    async resetSolves() {
      const answer = confirm("Are you sure?");
      if (answer) await this.$store.dispatch({ type: "resetSolves" });
    },
    onAddSolve(solveResult) {
      this.$store.dispatch({ type: "addSolve", solveResult });
      this.loadScramble();
    },

    getAverage(solves) {
      const onlyTimes = solves.map((solve) => {
        return solve.solveResult;
      });
      const slowest = Math.min(...onlyTimes);
      const fastest = Math.max(...onlyTimes);
      const removedSlowAndFast = onlyTimes.filter((time) => {
        return time !== slowest && time !== fastest;
      });
      const solvesSum = removedSlowAndFast.reduce((acc, time) => {
        return acc + time;
      }, 0);
      const avg = solvesSum / (solves.length - 2);
      return Math.floor(avg);
    },
  },

  components: {
    Scramble,
    Timer,
    SolvesTable,
    Notification,
  },
  computed: {
    solvesForDisplay() {
      const solves = this.$store.getters.solvesForDisplay;
      const updatedSolves = solves.map((solve, idx) => {
        const avgOfFive =
          idx >= 4 ? this.getAverage(solves.slice(idx - 4, idx + 1)) : null;
        const avgOfTwelve =
          idx >= 11 ? this.getAverage(solves.slice(idx - 11, idx + 1)) : null;
        return { ...solve, avgOfFive, avgOfTwelve };
      });
      return updatedSolves;
    },
    scrambleForDisplay() {
      return this.$store.getters.scramble;
    },
    bestSolve() {
      const solves = this.$store.getters.solvesForDisplay;
      if (!solves.length) return 0;
      const times = solves.map((solve) => {
        return solve.solveResult;
      });
      return Math.min(...times);
    },
    notification() {
      return this.$store.getters.notification;
    },
  },
  // watch: {
  //   $route(currRoute, prevRoute) {
  //     if (currRoute.fullPath === "/") {
  //       this.$store.dispatch({ type: "loadSolves" });
  //     }
  //   },
  // },
};
</script>

