<template>
  <div class="time-container">
    <div :class="timerColorClasses" class="timer-inner">
      <span v-if="time.minutes">{{ time.minutes }}:</span>
      <span>{{ time.seconds }}.</span>
      <span class="milliseconds">{{ millisecondsForDisplay }}</span>
    </div>
    <div v-if="isWin" :class="{ win: isWin }" class="finish-solve-circle"></div>
  </div>
</template>

<script>
export default {
  props: {
    onAddSolve: Function,
    bestSolve: Number,
  },
  created() {
    window.addEventListener("keydown", this.startInspection);
    window.addEventListener("keydown", this.stopTimer);
    window.addEventListener("keyup", this.runTimer);
  },
  unmounted() {
    window.removeEventListener("keydown", this.startInspection);
    window.removeEventListener("keydown", this.stopTimer);
    window.removeEventListener("keyup", this.runTimer);
    clearInterval(this.inspection.intervalId);
    clearInterval(this.intervalId);
  },

  data() {
    return {
      initialTime: null,
      solveResult: null,
      time: {
        milliseconds: 0,
        seconds: 0,
        minutes: 0,
      },
      inspection: {
        isRed: false,
        isGreen: false,
        intervalId: null,
        time: null,
      },
      isRunning: false,
      intervalId: null,
      isWin: false,
    };
  },

  methods: {
    runTimer(ev) {
      if (ev.code === "Space" && !this.isRunning && this.inspection.isGreen) {
        this.stopInspection();
        this.startTimer();
      }
    },

    startTimer() {
      this.initialTime = Date.now();
      this.isRunning = true;
      this.intervalId = setInterval(() => {
        const distance = Date.now() - this.initialTime;
        const milliseconds =
          Math.floor(distance % 1000) < 100
            ? "0" + Math.floor(distance % 1000)
            : Math.floor(distance % 1000);
        const seconds = Math.floor((distance % (1000 * 60)) / 1000);
        const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        this.time.milliseconds = milliseconds;
        this.time.seconds = seconds;
        this.time.minutes = minutes;
        this.solveResult = distance;
      }, 10);
    },

    stopTimer(ev) {
      if (ev.code === "Space" && this.isRunning) {
        this.isRunning = false;
        clearInterval(this.intervalId);
        if (this.solveResult < this.bestSolve) {
          this.$store.dispatch({
            type: "showNotification",
            txt: "Congratulations! It's a new record!",
          });
          this.toggleWinAnimation();
        }
        this.$emit("onAddSolve", this.solveResult);
      }
    },

    startInspection(ev) {
      if (ev.code !== "Space" || this.inspection.intervalId || this.isRunning)
        return;
      this.stopInspection(); //Reset inspection before starting again
      this.inspection.time = Date.now();
      this.inspection.isRed = true;
      this.inspection.intervalId = setInterval(() => {
        const distance = Date.now() - this.inspection.time;
        if (distance > 500) {
          this.inspection.isRed = false;
          this.inspection.isGreen = true;
        }
      }, 100);
    },

    stopInspection() {
      clearInterval(this.inspection.intervalId); //Stop inspection
      this.inspection.intervalId = null;
      this.inspection.isRed = false;
      this.inspection.isGreen = false;
    },
    toggleWinAnimation() {
      this.isWin = true;
      setTimeout(() => {
        this.isWin = false;
      }, 800);
    },
  },
  computed: {
    parsedMilliSeconds() {
      return Math.floor(this.time.milliseconds / 100);
    },
    millisecondsForDisplay() {
      if (this.isRunning) return this.parsedMilliSeconds;
      else return this.time.milliseconds;
    },
    timerColorClasses() {
      return {
        "red-timer": this.inspection.isRed,
        "green-timer": this.inspection.isGreen,
      };
    },
  },
};
</script>

