<template>
  <router-link :to="`solve/${solve._id}`">
    <div @click="onSelectSolve" class="solves-table-row">
      <span>{{ idx + 1 }}</span>
      <span>{{ penalty }}</span>
      <span v-if="solve.avgOfFive">
        {{ getFormattedTime(solve.avgOfFive) }}</span
      >
      <span class="blank-avg" v-else>---</span>
      <span v-if="solve.avgOfTwelve">
        {{ getFormattedTime(solve.avgOfTwelve) }}</span
      >
      <span class="blank-avg" v-else>---</span>
    </div>
  </router-link>
</template>

<script>
import { utilService } from "../services/utilService";
export default {
  props: {
    solve: Object,
    idx: Number,
  },
  methods: {
    getFormattedTime(time) {
      return utilService.getFormattedTime(time);
    },
    onSelectSolve() {
      this.$emit("selectSolve", this.solve._id);
    },
  },
  computed: {
    penalty() {
      switch (this.solve.penalty) {
        case "good":
          return this.getFormattedTime(this.solve.solveResult);
        case "plusTwo":
          return this.getFormattedTime(this.solve.solveResult) + "+2";
        case "dnf":
          return "DNF";
        default:
          break;
      }
    },
  },
};
</script>

