<template>
  <div class="comments-container">
    <form @submit.prevent="onAddComment">
      <textarea
        v-model="txt"
        placeholder="Add a comment..."
        name=""
        id=""
        cols="30"
        rows="5"
      ></textarea>
      <button>Submit</button>
    </form>
    <div class="comments-box">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    addComment: Function,
  },
  data() {
    return {
      txt: "",
    };
  },
  methods: {
    onAddComment() {
      this.$emit("addComment", this.txt);
      this.txt = "";
    },
  },
};
</script>

