import { scrambleService } from '../../services/scrambleService.js'
export default {
    state: {
        scramble: null,
    },
    getters: {
        scramble(state) {
            return state.scramble
        }
    },
    mutations: {
        setScramble(state, payload) {
            state.scramble = scrambleService.getNewScramble();
        }
    },
    actions: {
        loadScramble(context) {
            context.commit({ type: 'setScramble', scramble })
        }
    }
}