<template>
  <div
    v-if="solve"
    @click.self="onCloseSolve()"
    class="solve-setails-container"
  >
    <div class="inner">
      <div>
        <i @click="onCloseSolve()" class="fa fa-arrow-left close-details"></i>
      </div>
      <div class="details-content">
        <div>
          <h3 class="solve-number">Solve No.{{ idx + 1 }}</h3>
          <div class="divider"></div>
        </div>
        <h3>
          Time: <span>{{ penalty }}</span>
        </h3>
        <h3>
          Scramble: <span>{{ solve.scramble }}</span>
        </h3>
        <div class="penalties">
          <h3>Penalty:</h3>
          <input
            @click="updatePenalty"
            value="good"
            type="radio"
            name="penalties"
            id="good"
            :checked="solve.penalty === 'good'"
          />
          <label for="good">Good</label>
          <input
            @click="updatePenalty"
            value="plusTwo"
            type="radio"
            name="penalties"
            id="plusTwo"
            :checked="solve.penalty === 'plusTwo'"
          />
          <label for="plusTwo">+2</label>
          <input
            @click="updatePenalty"
            value="dnf"
            type="radio"
            name="penalties"
            id="dnf"
            :checked="solve.penalty === 'dnf'"
          />
          <label for="dnf">DNF</label>
        </div>
        <comments @addComment="addComment">
          <ul>
            <li v-for="comment in solve.comments" :key="comment.id">
              <h4 v-formatDate>{{ comment.createdAt }}</h4>
              <h4>{{ comment.txt }}</h4>
            </li>
          </ul>
        </comments>
        <button class="delete" @click="onDeleteSolve">Delete solve</button>
      </div>
    </div>
  </div>
</template>

<script>
import { utilService } from "../services/utilService";
import Comments from "./Comments.vue";
export default {
  data() {
    return {
      solve: null,
      idx: null,
    };
  },
  created() {
    this.loadSolve();
  },

  methods: {
    async loadSolve() {
      const { solveId } = this.$route.params;
      await this.$store.dispatch({ type: "getSolveById", solveId });
      this.solve = this.$store.getters.selectedSolve;
      this.idx = this.$store.getters.solvesForDisplay.findIndex(
        (solve) => solve._id === solveId
      );
    },
    onCloseSolve() {
      this.$router.push("/");
    },
    getFormattedTime(time) {
      return utilService.getFormattedTime(time);
    },
    onDeleteSolve() {
      const answer = confirm("Are you sure?");
      if (answer) {
        this.$store.dispatch({ type: "removeSolve", solveId: this.solve._id });
        this.onCloseSolve();
      }
    },
    async updatePenalty(ev) {
      const { value } = ev.target;
      await this.$store.dispatch({ type: "updatePenalty", penalty: value });
      this.loadSolve();
    },
    async addComment(comment) {
      await this.$store.dispatch({
        type: "addComment",
        comment,
        solveId: this.solve._id,
      });
      this.loadSolve();
    },
  },
  computed: {
    penalty() {
      switch (this.solve.penalty) {
        case "good":
          return this.getFormattedTime(this.solve.solveResult);
        case "plusTwo":
          return this.getFormattedTime(this.solve.solveResult) + "+2";
        case "dnf":
          return "DNF";
        default:
          break;
      }
    },
  },
  components: {
    Comments,
  },
};
</script>

