import { asyncStorageService } from './asyncStorageService.js';
import { utilService } from './utilService.js';

const SOLVES_KEY = 'solves_DB'

export const solveService = {
  query,
  getSolveById,
  removeSolve,
  addSolve,
  updateSolve,
  addComment,
  resetSolves
}


async function query() {
  try {
    const solves = await asyncStorageService.query(SOLVES_KEY)
    return solves;
  } catch (err) {
    console.log('Couldn\'t find solves', err);
  }
}

async function getSolveById(solveId) {
  try {
    const solve = await asyncStorageService.get(SOLVES_KEY, solveId)
    return solve
  } catch (err) {
    console.log('Couldn\'t find solve', err);
  }
}

async function addSolve(newSolve) {
  try {
    const solve = await asyncStorageService.post(SOLVES_KEY, newSolve)
    return solve
  } catch (err) {
    console.log('Couldn\'t add solve', err);
  }
}

async function updateSolve(solveToUpdate) {
  try {
    const solve = await asyncStorageService.put(SOLVES_KEY, solveToUpdate)
    return solve
  } catch (err) {
    console.log('Couldn\'t update solve', err);
  }
}


async function addComment(commentToSave, solveId) {
  try {
    const solve = await getSolveById(solveId)
    const comment = {
      txt: commentToSave,
      createdAt: Date.now(),
      id: utilService.makeId()
    }
    solve.comments = [...solve.comments, comment]
    await asyncStorageService.put(SOLVES_KEY, solve)
    return commentToSave
  } catch (err) {
    console.log('Couldn\'t add comment', err);
  }
}

async function removeSolve(solveId) {
  try {
    await asyncStorageService.remove(SOLVES_KEY, solveId)
  } catch (err) {
    console.log('Couldn\'t remove solve', err);
  }
}

async function resetSolves() {
  try {
    await asyncStorageService.reset(SOLVES_KEY)
  } catch (err) {
    console.log('Couldn\'t reset solves', err);
  }
}